.guest-list{
	li{
		display:block;
		vertical-align:middle;
		padding: 7px 0px;
		list-style: none;
	}
}

.btn{
	padding: 3px 10px;
	margin: 0px 4px;
}

[data-action="selectDay"]{
	cursor:pointer;
}

[data-action="next"],
[data-action="previous"]{
	display:block;
	cursor:pointer;
}

.bootstrap-datetimepicker-widget{
	font-size: 12px !important;
}

.cover-img{
	width: 100%;
	height: 340px;
	background-repeat: no-repeat;
    background-position: center; 
}

.navbar{
	margin-bottom: 0px;
	a{
		color:#FFF;
		font-weight: bold;
	}
	.navbar-nav {
		display: inline-block;
		float: none;
		vertical-align: top;
	}
	.navbar-collapse {
		text-align: center;
	}
}


.jumbo{
	background-image: url('/img/homepage-hero-1.jpg');
	background-position: center;
	.tint{
		background: rgba(8, 31, 55, 0.8);
	}
	.title{
		padding: 90px 100px 110px;
		h1{
			color:white;
			font-weight: bold;
			font-size: 60px;
		}
		p{
			color:#C3C7CB;
			padding-bottom: 20px;
		}
		.btn{
			padding:10px 25px;
			font-size: 15px;
			font-weight: bold;
		}
	}
}

.search{
	background-color: #DADADA;
	padding: 20px 0 25px;
	.form-control{
		height: 52px;
		margin-left:40px;
		width: 95%;
	}
	.btn{
		color:#FFF;
		padding:14px 15px;
		background: #1E7AD1;
	}
}

.search-login{
	padding:10px;
	font-weight: bold;
}

.upcoming{
	padding: 35px 0 20px;
	color:#A3A1A1;
	font-size:16px;
}

.thumbnail{
	width: 240px;
	height: 240px;
	background-size: contain;
	margin:0;
}

.card-block{
	font-size: 12px;
	margin-bottom: 30px;
	h5{
		margin: 12px 0 5px;
		font-size: 12px;
	}
	.card-title{
		margin: 0px;
		font-size: 18px;
		margin-bottom: 7px;
		a{
			color: #000;
		}
	}
	span{
		margin-bottom: 20px;
	}
}

.last-call{
	font-size: 40px;
	font-weight: bold;
	padding: 50px 0 150px;
}

.get-started{
	padding: 10px 15px;
	font-size: 14px;
	font-weight: bold;
}

.footer{
	background-color: #081F37;
	padding: 50px;
	a{
		color: #FFF;
		font-weight: bold;
	}
}

.restriction{
	color:#808182;
}

.cover-edit{
	width: 100%;
	height: 340px;
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat; 
}